@charset 'UTF-8';
@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');
@import "variables";
@import "keyframes";
@import "mixin";
@import "media-queries";
@import "base";

:root {
    --color-main: #f12b4e;
    --color-main2: #c21634;
}

/* Main */

body {
    font-family: $fontFamily;
    font-size: $fontSize;
    font-weight: 400;
    // background-color: #f5f5f5;
}
img {
    max-width: 100%;
    height: auto;
    vertical-align: top;
}
a,
input,
textarea {
    outline: none;
    padding: 0px;
}
input[type="number"] {
    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }
}
.w-clear:after {
    display: block;
    content: "";
    clear: both;
}
.hidden {
    display: none;
}
.clear {
    clear: both;
}
.none {
    display: none;
}
.hidden-seoh {
    visibility: hidden;
    height: 0px;
    margin: 0px;
    overflow: hidden;
}

.wrap-content {
    max-width: 1326px;
    margin: 0 auto;
    padding: 0px 15px;
    width: 100%;
}
.wrap-content-detail {
    max-width: 1130px;
    margin: 0 auto;
    padding: 0px 15px;
    width: 100%;
}
.wrap-home {
    margin: 0 auto 40px;
    // background: #fff;
    .wrap-thongbao {
        margin: -40px auto;
    }
}

/* Transition All */
.transition {
    -webkit-transition: 0.3s all;
    -moz-transition: 0.3s all;
    -o-transition: 0.3s all;
    transition: 0.3s all;
}

/* Scale IMG */
.scale-img {
    overflow: hidden;
    display: block;
    -webkit-transition: 0.5s !important;
    -moz-transition: 0.5s !important;
    -o-transition: 0.5s !important;
    transition: 0.5s !important;
    img {
        -moz-transform: scale(1, 1);
        -webkit-transform: scale(1, 1);
        -o-transform: scale(1, 1);
        -ms-transform: scale(1, 1);
        transform: scale(1, 1);
        -webkit-transition: 0.5s !important;
        -moz-transition: 0.5s !important;
        -o-transition: 0.5s !important;
        transition: 0.5s !important;
    }
    &:hover > img {
        -moz-transform: scale(1.1, 1.1);
        -webkit-transform: scale(1.1, 1.1);
        -o-transform: scale(1.1, 1.1);
        -ms-transform: scale(1.1, 1.1);
        transform: scale(1.1, 1.1);
    }
}
/* General */
.title-main {
    margin-bottom: 1.5rem;
    @include flex(center, space-between);
    // border-bottom: 1px solid #e0e0e0;
    margin: 0 0 15px;
    @include media(SM) {
        margin: 0 0 15px;
    }
    span,h2,h1,h3 {
        text-transform: uppercase;
        color: var(--color-main);
        font-size: 30px;
        font-weight: 700;
        @include media(SM) {
            font-size: 15px;
        }
    }
    a {
        color: var(--color-main);
        transition: 0.3s;
        &:hover {
            color: var(--color-main);
            text-decoration: none;
        }
    }

}
.time-main {
    margin-bottom: 0.75rem;
    color: #999999;
    i {
        vertical-align: top;
        margin: 3px 7px 0px 0px;
    }
    span {
        vertical-align: top;
        display: inline-block;
    }
}
.share {
    padding: 17px 0 10px 0;
    line-height: normal;
    /* background: rgba(128, 128, 128, 0.15); */
    margin-top: 15px;
    border-radius: 5px;
    b {
        display: block;
        margin-bottom: 5px;
    }
}
.control-owl {
    position: absolute;
    width: 100%;
    z-index: 2;
    left: 0px;
    top: calc(50% - 45px / 2);
    button {
        opacity: 0.5;
        top: 0px;
        position: absolute;
        outline: none;
        border: 0px;
        padding: 0px;
        margin: 0px;
        display: block;
        cursor: pointer;
        color: #ffffff;
        width: 45px;
        height: 45px;
        text-align: center;
        font-size: 25px;
        margin: 0px;
        background-color: #000000;
        border-radius: 3px;
        @include flex(center, center);

        &:hover {
            opacity: 1;
        }
        &.owl-prev {
            left: 20px;
        }
        &.owl-next {
            right: 20px;
        }
    }
}
.form-control,
.form-control-plaintext:not(textarea),
.custom-select {
    height: calc(1em + 1.25rem + 8px);
}
.custom-form-row {
    margin-right: -10px;
    margin-left: -10px;
    > {
        .col,
        [class*="col-"] {
            padding-right: 10px;
            padding-left: 10px;
        }
    }
}
.text-sm {
    font-size: 0.875rem !important;
}

/* Lazyload */
img {
    &.lazy {
        opacity: 0;
    }
    &:not(.initial) {
        transition: opacity 1s;
    }
    &.initial,
    &.loaded,
    &.error {
        opacity: 1;
    }
    &:not([src]) {
        visibility: hidden;
    }
}


/* Header */
.header {
    // box-shadow: 0 0px 9px 1px #ffffff33;
    background: transparent;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 12;
    .d-flex {
        justify-content: space-between;
        align-items: center;
        @include media(MD) {
            justify-content: center;
        }
    }
    &.fixed {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 12;
        box-shadow: 0 0px 9px 1px #ffffff33;
        background: #5f5f5f;
        .header-top {
            display: none;
        }
        .header-bottom {
            padding: 5px 0;
        }
    }
}
.header-top {
    font-size: 15px;
    color: #fff;
    padding: 15px 0 15px;
    border-bottom: 1px solid #ffffff33;
    ul {
        list-style: none;
        padding: 0;
        margin: 0;
    }
    @include media(MD) {
        display: none;
    }
    .con-left {
        @include flexbox();
    }
    .hotline {
        background: url(../images/icon-hotline.png) no-repeat left center;
        padding-left: 25px;
        margin-right: 15px;
    }
    .email {
        background: url(../images/icon-email.png) no-repeat left center;
        padding-left: 25px;
    }
    .social {
      ul {
        @include flexbox();
        margin-left: 20px;
        li {
          margin: 0 5px;
        }
      }
    }

}
.con-right {
    @include flexbox();
    align-items: center;
    justify-content: center;
    @include media(MD) {
        padding: 20px;
    }
    li {
        &.lang {
          background: url(../images/icon-eng.png) no-repeat right center;
          padding-right: 25px;
          margin-right: 30px;
        }
        &.login {
          background: url(../images/icon-login.png) no-repeat left center;
          padding-left: 25px;
        }
    }
}
.header-bottom {
  padding: 15px 0;
  transition: 0.3s;
  @include media(MD) {
    display: none;
  }
}
.logo-header {
    img {
        max-height: 65px;
        @include media(LG) {
            max-height: 50px;
        }
        @include media(MD) {
            max-height: 45px;
        }
    }
}
.social-header{
    margin: 0;
    padding: 0;
    @include media(XL) {
        display: none;
    }
    li {
        margin-left: 15px;
        width: 30px;
    }
}
//breadCrumbs
.breadCrumbs {
    background-color: transparent;
    &-sub {
        .breadcrumb {
            margin-bottom: 0;
        }
    }
    li {
        a {
            color: #333;
        }
    }
    .breadcrumb {
        background: transparent;
        padding: 0.75rem 0;
    }
}
/* Menu */
.menu {
    position: relative;
    z-index: 100;
    @include media(MD) {
        display: none;
    }
    ul {
        width: 100%;
        padding: 0px;
        margin: auto;
        list-style: none;
        &.menu-main {
            @include flex(center, space-between);
        }
        li {
            position: relative;
            z-index: 1;
            // margin: 0 5px;
            padding-left: 30px;
            &.menu-line {
                width: 1px;
                height: 15px;
                background-color: rgba(0, 0, 0, 0.1);
            }
            a {
                display: block;
                position: relative;
                font-size: 15px;
                color: #fff;
                padding: 0;
                text-decoration: none;
                border-bottom: 1px solid transparent;
                text-transform: uppercase;
                transition: 0.3s;
                @include media(LG) {
                    font-size: 12px;
                    padding: 5px;
                }
                &.active, &:hover {
                    // font-weight: bold;
                    border-bottom: 1px solid;
                }
                &.has-child {
                    padding-right: 16px;
                    margin-right: 8px;
                    &:after {
                        content: "";
                        position: absolute;
                        bottom: calc(50% - 4px / 2);
                        right: 5px;
                        width: 5px;
                        height: 5px;
                        border: 1px solid #fafafa;
                        border-top: 0px;
                        border-left: 0px;
                        -moz-transform: rotate(45deg);
                        -webkit-transform: rotate(45deg);
                        -o-transform: rotate(45deg);
                        -ms-transform: rotate(45deg);
                        transform: rotate(45deg);

                    }
                }
            }
            ul {
                position: absolute;
                min-width: 250px;
                padding-top: 0.75rem;
                padding-bottom: 0.75rem;
                background-color: #fafafa;
                border-radius: 0.25rem;
                -webkit-box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.15);
                box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.15);
                -webkit-transform: perspective(600px) rotateX(-90deg);
                transform: perspective(600px) rotateX(-90deg);
                -webkit-transform-origin: 0 0 0;
                transform-origin: 0 0 0;
                opacity: 0;
                visibility: hidden;

            }
            &:hover > ul {
                -webkit-transform: perspective(600px) rotateX(0);
                transform: perspective(600px) rotateX(0);
                transform-origin: 0 0 0;
                -webkit-transform-origin: 0 0 0;
                opacity: 1;
                visibility: visible;

            }
            ul li {
                padding-left: 0.75rem;
                padding-right: 0.75rem;
                a {
                    font-size: 14px;
                    color: #313131;
                    border-bottom: 1px solid #ececec;
                    padding: 10px 0px 9px 0px;
                    text-transform: capitalize;
                    border-radius: 0;
                    &.has-child {
                        margin-right: 0px;
                        &:after {
                            border-color: #313131;
                            -moz-transform: rotate(-45deg);
                            -webkit-transform: rotate(-45deg);
                            -o-transform: rotate(-45deg);
                            -ms-transform: rotate(-45deg);
                            transform: rotate(-45deg);
                        }
                    }
                    &.active {
                        color: var(--color-main);
                    }
                    &.active.has-child:after {
                        border-color: var(--color-main);
                    }
                }
                &:last-child > a {
                    border-bottom: 0px;
                }
                &:hover:last-child > a {
                    border-bottom: 0px;
                }
                &:hover > a {
                    color: var(--color-main);
                    border: none;
                    border: 1px solid transparent;
                    border-bottom: 1px solid #ececec;
                    &.has-child:after {
                        //border-color: var(--color-main);
                    }
                }
                ul {
                    top: -0.75rem;
                    left: 100%;
                    margin-top: 0px;
                }
            }
        }
    }
}
.c-btn {
  a, button {
    padding: 10px 24px 10px 24px;
    border-radius: 10px;
    font-size: 16px;
    line-height: 21px;
    background: #179B42;
    color: #fff;
    border: 1px solid #179B42;
    display: inline-block;
  }
  span {
    background: url(../images/icon-booking.png) no-repeat right center;
    padding-right: 25px;
  }
  &.c-btn2 {
    a, button {
      background: transparent;
    }
  }
  &.c-btn3 {
    a, button {
      background: #fff;
      color: #179B42;
      border-radius: 5px;
      padding: 8px 24px;
    }
    span {
      background: url(../images/icon-booking2.png) no-repeat right center;

    }
  }
}

/* Mmenu */
.menu-res {
    height: 55px;
    z-index: 100;
    background: var(--color-main);
    position: relative;
    display: none;
    @include media(MD) {
        display: block;
    }
}
.menu-bar-res {
    height: 55px;
    padding: 0px 15px;
    @include flex(center, space-between);
}
#menu {
    display: none;
}
#hamburger {
    display: block;
    width: 45px;
    height: 23px;
    position: relative;
    &:before,
    &:after,
    span {
        background: #fff;
        content: "";
        display: block;
        width: 100%;
        height: 3px;
        position: absolute;
        left: 0px;
    }
    &:before {
        top: 0px;
    }
    span {
        top: 10px;
    }
    &:after {
        top: 20px;
    }
    &:before,
    &:after,
    span {
        -webkit-transition: none 0.5s ease 0.5s;
        transition: none 0.5s ease 0.5s;
        -webkit-transition-property: transform, top, bottom, left, opacity;
        transition-property: transform, top, bottom, left, opacity;
    }
}
.mm-wrapper_opening #hamburger {
    &:before,
    &:after {
        top: 10px;
    }
    span {
        left: -50px;
        opacity: 0;
    }
    &:before {
        transform: rotate(45deg);
    }
    &:after {
        transform: rotate(-45deg);
    }
}
.mm-menu_opened {
    display: block !important;
}
.mm-slideout {
    z-index: unset;
}

/* Search Responsive */
.search-res {
    position: relative;
    .icon-search {
        width: 40px;
        height: 40px;
        cursor: pointer;
        text-align: center;
        line-height: 42px;
        color: #fff;
        font-size: 17px;
        margin: 0px;
        border: 1px solid var(--color-main);
        border-radius: 100%;
        @include flexbox();
        align-items: center;
        justify-content: center;
        &.active {
            border: 1px solid #fff;
            border-radius: 100%;
        }
    }
    .search-grid {
        position: absolute;
        top: 50px;
        right: 0px;
        width: 0px;
        height: 40px;
        overflow: hidden;
        background: #fff;
        border: 1px solid var(--color-main);
        z-index: 2;
        opacity: 0;
        border-radius: 25px;
        line-height: normal;
        p {
            float: left;
            width: 35px;
            height: 38px;
            cursor: pointer;
            outline: none;
            border: none;
            margin: 0px;
            font-size: 17px;
            display: block;
            color: #ec2d3f;
            line-height: 40px;
            text-align: center;
        }
        input {
            width: calc(100% - 35px);
            float: right;
            line-height: 38px;
            outline: none;
            border: none;
            color: #ec2d3f;
            &::-webkit-input-placeholder {
                color: #313131;
            }
            &:-moz-placeholder {
                color: #313131;
            }
            &::-moz-placeholder {
                color: #313131;
            }
            &:-ms-input-placeholder {
                color: #313131;
            }
        }
    }
}
// Slideshow
.slideshow {
    position: relative;
    &.slidenone {
        .owl-page {
            display: none;
        }

        .info-slideshow {
            display: none;
        }
    }
}
.slideshow-item {
    position: relative;
}

.info-slideshow {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  @include flex(center);
  opacity: 0;
  -webkit-transform: translate(150px,0);
  -moz-transform: translate(150px,0);
  -ms-transform: translate(150px,0);
  -o-transform: translate(150px,0);
  transform: translate(150px,0);
  -webkit-transition: all 0.8s ease;
  -moz-transition: all 0.8s ease;
  -o-transition: all 0.8s ease;
  transition: all 0.8s ease;
  color: #fff;
  @include SP{
    position: relative;
    color: #1A1A1A;
    padding: 20px 0;
    .c-btn.c-btn2 {

      a {
        color: #179B42;
      }
      span {
        background: url(../images/icon-booking2.png) no-repeat right center;
      }
    }
  }
  .wrap-content {
    max-width: 600px;
    margin: initial;
    @include SP{
      max-width: 100%;
    }
  }
  .ttl {
    letter-spacing: 3px;
    font-size: 18px;
    line-height: 27px;
    font-weight: 500;
    font-family: "Poppins", sans-serif;
    color: #179B42;
    text-transform: uppercase;
    margin-bottom: 12px;
    @include SP {
      font-size: 15px;
      line-height: 24px;
    }
  }
  .ttl2 {
    font-family: "Poppins", sans-serif;
    font-size: 44px;
    font-weight: 700;
    line-height: 54px;
    text-transform: uppercase;
    margin-bottom: 24px;
    @include SP {
      font-size: 20px;
      line-height: 28px;
      margin-bottom: 14px;
    }
  }
  .desc {
    font-size: 15px;
    line-height: 24px;
  }
}
.owl-item.active {
    .info-slideshow {
        opacity: 1;
        -webkit-transition-delay: 0.8s;
        -moz-transition-delay: 0.8s;
        -o-transition-delay: 0.8s;
        transition-delay: 0.8s;
        -webkit-transform: translate(0,0);
        -moz-transform: translate(0,0);
        -ms-transform: translate(0,0);
        -o-transform: translate(0,0);
        transform: translate(0,0);
    }
}

.box-btn {
