//wrap-about
.wrap-about {
    padding: 120px 0;
    @include font(15,24,0);
    position: relative;
    z-index: 1;
    .title-global {
      margin-bottom: 30px;
    }
    .desc {
      margin-bottom: 30px;
    }
    .content {
      margin-bottom: 40px;
