        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}


/* Plugbar */
.plugbar {
    position: fixed;
    bottom: 0;
    left: 0;
    border-top: 1px solid #eae6e6;
    width: 100%;
    right: 0;
    max-width: 767px;
    margin: 0 auto;
    background: #ffffff;
    padding: 9px 10px 10px 7px;
    z-index: 123;
    ul {
        list-style: none;
        padding: 0;
        margin: 0px;
        @include flex(center, space-between);
        li {
            width: 20%;
            float: left;
            a {
                display: block;
                color: #696969;
                font-size: 12px;
                text-align: center;
                position: relative;
                i {
                    font-size: 20px;
                    color: #696969;
                }
                span {
                    position: absolute;
                    width: 50px;
                    height: 50px;
                    top: -40px;
                    left: calc(50% - 50px / 2);
                    font-size: 14px;
                    display: block;
                    border: 1px solid rgba(234, 230, 230, 0.5);
                    border-radius: 100%;
                }
            }
        }
    }
}

.mobile_menu_section {
