  }

  .mobile_menu_section .mobile_menu_help svg {
    margin-right: 16px;
    width: 24px;
    height: 24px;
  }
.content-main {
    img {
        height: auto !important;
        max-width: 100%;
    }
}
.breadcrumb {
    margin-bottom: 1rem;
}

//Tour
.item-tour {
  border-radius: 16px;
  overflow: hidden;
  background: #fff;
  position: relative;
  margin-bottom: 30px;
  .img-tour {
    img {
      width: 100%;
    }
  }
  .price-per {
    position: absolute;
    top: 10px;
    left: 10px;
    background: #E83E62;
    color: #fff;
    padding: 2px 10px 2px 10px;
    border-radius: 8px;
    font-family: "Poppins", sans-serif;
    @include font(14,28,0,600);
    z-index: 1;
  }
  .lst-utilities {
    background: #179B42;
    @include flexbox();
    @include align-items();
    @include justify-content();
    max-width: 90%;
    height: 52px;
    border-radius: 10px;
    margin: -26px auto 20px;
    position: relative;
    z-index: 1;
    li {
      @include font(14,20,0,400);
      color: #fff;
      &:nth-child(2) {
        padding: 0 12px;
        margin: 0 12px;
        border-left: 1px solid #FFFFFF33;
        border-right: 1px solid #FFFFFF33;
      }
    }
  }
  .cont-tour {
    padding: 0 20px 20px;
  }
  .ttl-tour {
    font-family: "Poppins", sans-serif;
    @include font(17,27,0,700);
    margin-bottom: 12px;
  }
  .price-tour {
    border-top: 1px solid #87878733;
    padding: 20px 0 0 0;
    font-family: "Poppins", sans-serif;
    @include flexbox();
    @include align-items(center);
    .price-new {
      @include font(24,38,20,700);
      color: #E83E62;
      margin-right: 10px;
    }
    .price-old {
      color: #878787;
      @include font(18,28,0);
      text-decoration: line-through;
    }
    .price-per--v2 {
      @include font(14,28,0,600);
      background: #179B42;
      color: #fff;
      height: 32px;
      border-radius: 8px;
      padding: 2px 10px 2px 10px;
      margin-left: 16px;
      min-width: 50px;
      text-align: center;
    }
  }
}

.rating {
  width: 100%;
  display: flex;
  font-size: 12px;
  margin-bottom: 20px;
  align-items: center
}

.rating .count {
  margin: 0 3px 0 5px;
  @include font(18,28,0,400);
  font-family: "Poppins", sans-serif;
  color: #878787;
}

.rating .star-rating {
  position: relative;
  z-index: 0;
  display: inline-block;
  white-space: nowrap;
  color: #ccc;
  font-size: 18px
}

.rating .star-rating:before,.rating .star-rating:after {
  content: "★ ★ ★ ★ ★"
}

.rating .star-rating:after {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  overflow: hidden;
  white-space: nowrap;
  color: #FFB800
}

.rating .star-rating[data-rate="5"]:after {
  width: 100%
}

.rating .star-rating[data-rate="4.5"]:after {
  width: 90%
}

.rating .star-rating[data-rate="4"]:after {
  width: 80%
}

.rating .star-rating[data-rate="3.5"]:after {
  width: 70%
}

.rating .star-rating[data-rate="3"]:after {
  width: 60%
}

.rating .star-rating[data-rate="2.5"]:after {
  width: 50%
}

.rating .star-rating[data-rate="2"]:after {
  width: 40%
}

.rating .star-rating[data-rate="1.5"]:after {
  width: 30%
}

.rating .star-rating[data-rate="1"]:after {
  width: 20%
}

.rating .star-rating[data-rate="0.5"]:after {
  width: 10%
}

.rating .star-rating[data-rate="0"]:after {
  width: 0%
}